@charset "UTF-8";

:root {
    --bg: #28272d;
    --bg2: #333238;
    --bg3: #545a68;
    --bg4: #4c5164;
    --chatting: #3d4049;
    --border: #368b86;
    --orange: #f56327;
    --input: #a8a7ad;
}

html, body {
    height: 100%;
}
body {
    margin: 0;
    background-color: var(--bg);
}
.skip {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
}

.ct {
    position: relative;
    display: flex;
    max-width: 500px;
    min-height: 100%;
    max-height: 100%;
    flex-direction: column;
    margin: 0 auto;
}
.container {
    display: flex;
    flex: 1 ;
    min-height: 0px;

    &_overflow {
        flex: 1;
        overflow: auto;
    }
}
.item {
    list-style: none;
    border-radius: 5px;
    margin: 15px 0;
    background-color: var(--bg2);
    transition: background-color 0.1s ease, transform 0.1s ease;
    
    button {
        appearance: none;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 10px 10px;
        border: 0;
        border-radius: 0;
        background-color: transparent;
    }

    &_wrap {
        overflow: hidden;
        margin: 0;
        padding: 0 20px;
        min-height: 100%;
    }
    &_img {
        $size: 50px;
        position: relative;
        flex: 0 0 auto;
        width: $size;
        height: $size;
        margin-right: 10px;
        img {
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }
        @at-root .new &::after {
            $pos: 2px;
            content: '';
            position: absolute;
            top: -$pos;
            left: -$pos;
            width: $size + $pos;
            height: $size + $pos;
            border: 1px solid var(--border);
            border-radius: 100%;
            z-index: 1;
        }
    }
    &_text {
        overflow: hidden;
        margin: 0;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: white;
    }

    &.select, &:hover {
        transform: translate(-1px, -1px);
        background-color: gray;
        box-shadow: 3px 3px 10px black;
    }
} //item
.button_wrap {
    display: flex;
    height: 50px;
    min-height: 50px;

    button {
        appearance: none;
        flex: 1 1 auto;
        border: 0;
        border-radius: 0;
        background-color: var(--bg3);
        color: white;
        font-size: 15px;
        cursor: pointer;

        &:hover, &.selected {
            background-color: var(--orange);
        }
        + button {
            border-left: 1px solid var(--bg);
        }
        &.disabled {
            background-color: var(--bg2);
            color: var(--input);
        }
    } //button
} //button_wrap

.chatting {
    --pos: 25px;
    position: relative;
    margin-top: var(--pos);
    list-style: none;
    
    &_wrap {
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        margin: 0;
        padding: 0 25px;

        .item_img {
            position: absolute;
            top: calc(var(--pos) * -1);
            left: calc(var(--pos) * -1);
        }
    }

    &_text {
        max-width: 80%;
        padding: 10px 10px 10px 30px;
        border-radius: 10px;
        color: white;
        font-size: 14px;
        background-color: var(--bg4);
        line-height: 16px;
    }

    &.right {
        display: flex;
        justify-content: flex-end;

        .item_img {
            left: initial;
            right: calc(var(--pos) * -1);
        }
        .chatting_text {
            padding: 10px 30px 10px 10px;
            background-color: var(--chatting);
        }
    }
    &_box {
        position: relative;
        padding: 0;
    }
    &_input {
        appearance: none;
        resize: none;
        width: 100%;
        height: 40px;
        padding: 10px 35px 10px 10px;
        font-size: 14px;
        line-height: 18px;
        box-sizing: border-box;
        background-color: var(--input);
    }
    &_button {
        --size: 20px;
        --arrowSize: 5px;
        --border: 1px solid var(--borderColor);
        --borderColor: white;
        --arrow-left: 50%;
        --arrow-top: 6px;
        z-index: 1;
        position: absolute;
        top: calc(var(--size) / 2);
        right: calc(var(--size) / 2);
        appearance: none;
        border-radius: 100%;
        width: var(--size);
        height: var(--size);
        border: 0;
        background-color: var(--orange);
        cursor: pointer;

        &::after {
            content: '';
            position: absolute;
            top: var(--arrow-top);
            left: var(--arrow-left);
            width: var(--arrowSize);
            height: var(--arrowSize);
            border-top: var(--border);
            border-right: var(--border);
            transform: translateX(calc(var(--arrow-left) * -1)) rotate(-45deg);
        }
        &::before {
            content: '';
            position: absolute;
            top: var(--arrow-top);
            left: var(--arrow-left);
            width: 1px;
            height: calc(var(--arrowSize) * 2);
            background-color: var(--borderColor);
            transform: translateX(-0.5px)
        }
        &.backpage {
            left: 0;
            right: initial;
            transform: scale(1.3) rotate(-90deg);
            opacity: 0.7;
            transition: all 0.1s ease;
            &:hover {
                opacity: 1;
                transform: scale(1.6) rotate(-90deg);
            }
        }
    } //button
} //chatting

